.login-container {
    /* border: 1px solid black; */
    flex-direction: column;
}

.login-container h2 {
    display: flex;
    justify-content: center;
    font-size: 28px;
    margin: 10px
}
.form-container {
    /* border: 1px solid black; */
    
}

.form-input-container {
    flex-direction: column;
    /* border: 1px solid blue; */
}

.form-input-container * {
    margin: 5px;
}


.input-container {
    display: flex;

    margin: 0 auto;
    /* border: 1px solid orange; */
    width: 70%;
}
.input-container * {
    display: block;
}

label {
    display: block;
    /* border: 1px solid yellow; */
    margin: 0 auto;
    width: 100%;
}

.submit-buttons-container {
    flex-direction: column;
    /* border: 1px solid darkblue */
}

.submit-buttons-container * {
    margin: 5px;
}

.login-button {
    width: 300px;
    height: 30px;
    border-radius: 999px;
    border: 1px solid gray;
    cursor: pointer;
    transition: all 0.3s ease;
}
.login-button:hover {
    background-color: #e7e7e7;
    color: red;
    /* border: 1px solid blue; */
    
}
.login-button:focus {
    box-shadow: inset 0px 1px 2px #6e6e6e;
}


.sign-in-text-box {
    padding: 5px;
    border-radius: 6px;
    border: 1px solid gray;
    width: 90%;
}
/* 
input[type=password] {
    padding: 5px;
    border-radius: 6px;
    border: 1px solid gray
} */

.create-acc-line {
    /* border: 1px solid green; */
    margin: 5px;
}

.center {
    /* background-color: blue; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.errors {
    color: red;
    flex-direction: column;
    margin: 10px;
}

@media (min-width: 600px) {
    .login-container {
        flex-direction: column;
        width: 100vw;
        height: 80vh;
    }
    .input-container {
        display: flex;
        margin: 0 auto;
        /* border: 1px solid orange; */
        width: 300px;
    }
}